/*    */

.overlay {
//   /*    */
  opacity: 0;
  visibility: hidden;

  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 20;
  transition: 0.3s all;
}

/*     */

.modal.modal-readmore {
  opacity: 0;
  visibility: hidden;
  overflow: hidden;
}

/*        */

.overlay.active {
  opacity: 1;
  visibility: visible;
}

.modal.modal-readmore.active {
  opacity: 1;
  visibility: visible;
  overflow: hidden;

  transform: translate(-50%, -50%);
  position: fixed;
  width: 95%;
  height: 459px;
  top: 50%;
  left: 50%;
  z-index: 1000;
  box-shadow: 0 3px 10px -0.5px rgba(0, 0, 0, 0.2);
  text-align: center;

  background: $primary-text-color;
  border: 2px solid #f8d24e;
  border-radius: 10px;
  transition: 0.3s all;
  padding: 28px;

  @media screen and (min-width: 480px) {
    width: 280px;
    height: 459px;
    padding: 32px 60px;
  }

  @media screen and (min-width: 768px) {
    width: 568px;
    height: 449px;
  }
}

/*     */

.modal__cross {
  width: 11px;
  height: 11px;
  position: absolute;
  top: 16px;
  right: 16px;
  fill: $tittle-text-color;
  cursor: pointer;

  @media screen and (min-width: 768px) {
    width: 13px;
  height: 13px;
  }
}

 iframe {
   width: 100%;
   height: auto;
 }

.modal-readmore__title {
  font-family: 'Seymour One';
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.5;
  width: 171px;
  color: $tittle-text-color;


  margin-left: auto;
  margin-right: auto;

  // display: flex;
  // align-items: center;
  // justify-content: center;
  margin-bottom: 20px;

  @media screen and (min-width: 768px) {
     font-size: 20px;
  }
}

.modal-readmore__text {
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  height: 1,4;
  margin-top: 32 px;
  margin-bottom: 32px;
  text-align: left;
  color: $tittle-text-color;
  
  @media screen and (min-width: 768px) {
    font-size: 16px;
  }
}